import React from 'react';
import PropTypes from 'prop-types';
import withStyles from '@material-ui/core/styles/withStyles';
import { withTranslation } from 'react-i18next';
import { compose } from 'recompose';
import classNames from 'classnames';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import Country from './Country';
import HeaderProgress from '../ColumnMiddle/HeaderProgress';
import { cleanProgressStatus, isConnecting, isValidPhoneNumber } from '../../Utils/Common';
import { KEY_SUGGESTED_LANGUAGE_PACK_ID } from '../../Constants';
import ApplicationStore from '../../Stores/ApplicationStore';
import OptionStore from '../../Stores/OptionStore';
import LocalizationStore from '../../Stores/LocalizationStore';
import TdLibController from '../../Controllers/TdLibController';
import './SignInControl.css';

const styles = {
    button: {
        margin: '16px 0 0 0'
    },
    phone: {
        fontWeight: 'bold',
        textAlign: 'center'
    },
    continueAtLanguage: {
        // transform: 'translateY(100px)',
        textAlign: 'center',
        // position: 'absolute',
        cursor: 'pointer',
        // left: 0,
        // right: 0,
        // bottom: 0
    },
    root: {
        '& .MuiTextField-root': {
            margin: '12px 0',
            width: 360,
            [`& fieldset`]: {
                borderRadius: 8
            },
            [`& input`]: {
                padding: [17.5, 14]
            }
        },
        '& .MuiButton-root': {
            margin: '12px 0',
            padding: '15px 16px',
            width: 360,
            borderRadius: 8
        }
    }
};

class SignInControl extends React.Component {
    state = {
        connecting: isConnecting(ApplicationStore.connectionState),
        error: null,
        loading: false,
        suggestedLanguage: localStorage.getItem(KEY_SUGGESTED_LANGUAGE_PACK_ID),
        keep: true,
        phone: '',
        country: null
    };

    componentDidMount() {
        this.setSuggestedLanguagePackId();
        this.loadData();

        ApplicationStore.on('clientUpdateSetPhoneCanceled', this.onClientUpdateSetPhoneCanceled);
        ApplicationStore.on('clientUpdateSetPhoneError', this.onClientUpdateSetPhoneError);
        ApplicationStore.on('clientUpdateSetPhoneResult', this.onClientUpdateSetPhoneResult);
        ApplicationStore.on('updateConnectionState', this.onUpdateConnectionState);
        OptionStore.on('updateOption', this.onUpdateOption);
    }

    componentWillUnmount() {
        ApplicationStore.off('clientUpdateSetPhoneCanceled', this.onClientUpdateSetPhoneCanceled);
        ApplicationStore.off('clientUpdateSetPhoneError', this.onClientUpdateSetPhoneError);
        ApplicationStore.off('clientUpdateSetPhoneResult', this.onClientUpdateSetPhoneResult);
        ApplicationStore.off('updateConnectionState', this.onUpdateConnectionState);
        OptionStore.off('updateOption', this.onUpdateOption);
    }

    loadData = async () => {
        const input = 'json/countries.json';
        try {
            const response = await fetch(input);
            const data = await response.json();
            this.setState({ data: data.filter(x => x.emoji) });
        }
        catch (error) {
            console.error(error)
        }
    };

    onUpdateConnectionState = update => {
        const { state } = update;

        this.setState({ connecting: isConnecting(state) });
    };

    onClientUpdateSetPhoneCanceled = update => {
        this.setState({ loading: false });
    };

    onClientUpdateSetPhoneError = update => {
        const { error } = update;

        let errorString = null;
        if (error && error['@type'] === 'error' && error.message) {
            if (error.message === 'PHONE_NUMBER_INVALID') {
                this.setState({ error: { code: 'InvalidPhoneNumber' }, loading: false });
                return;
            } else {
                errorString = error.message;
            }
        } else {
            errorString = JSON.stringify(error);
        }

        this.setState({ error: { string: errorString }, loading: false });
    };

    onClientUpdateSetPhoneResult = update => {
        this.setState({ loading: false });
    };

    onUpdateOption = update => {
        const { name } = update;

        if (name === 'suggested_language_pack_id') {
            this.setSuggestedLanguagePackId();
        }
    };

    setSuggestedLanguagePackId = async () => {
        const { i18n } = this.props;
        if (!i18n) return;

        const languagePackId = OptionStore.get('suggested_language_pack_id');
        if (!languagePackId) return;

        const { value } = languagePackId;

        await LocalizationStore.loadLanguage(value);

        this.setState({ suggestedLanguage: value });
    };

    handleNext = () => {
        const { defaultPhone } = this.props;

        const phone = this.enteredPhone || defaultPhone;

        if (isValidPhoneNumber(phone)) {
            this.setState({ error: null, openConfirmation: true });
        } else {
            this.setState({ error: { code: 'InvalidPhoneNumber' } });
        }
    };

    handleKeyPress = event => {
        if (event.key === 'Enter') {
            event.preventDefault();
            this.handleDone();
        }
    };

    handleDone = () => {
        const { defaultPhone } = this.props;

        const phone = this.enteredPhone || defaultPhone;
        if (!isValidPhoneNumber(phone)) {
            this.setState({ error: { code: 'InvalidPhoneNumber' } });
            return;
        }

        this.setState({ error: null, loading: true });
        TdLibController.clientUpdate({
            '@type': 'clientUpdateSetPhone',
            phone
        });
    };

    handleChangeLanguage = () => {
        const { i18n } = this.props;
        const { suggestedLanguage } = this.state;

        if (!i18n) return;
        if (!suggestedLanguage) return;

        const nextLanguage =
            suggestedLanguage === i18n.language ? LocalizationStore.defaultLanguage : suggestedLanguage;

        TdLibController.clientUpdate({ '@type': 'clientUpdateLanguageChange', language: nextLanguage });
    };

    handleFilterOptions = (options, { inputValue }) => {
        if (!options) return;

        let value = inputValue.toLowerCase().replace(/ /g, '');
        value = value.length > 0 && value[0] === '+' ? value.substring(1) : value;

        return options.filter(x => this.isValidOption(x, value));
    };

    isValidOption = (x, value) => {
        if (!x) return false;
        if (!value) return true;

        const names = x.name.toLowerCase().split(' ');
        const phone = x.phones[0].replace(/ /g, '').replace('+', '').toLowerCase();

        if (names.some(x => x.startsWith(value))) return true;
        if (phone.startsWith(value)) return true;

        return false;
    };

    handleCountryChange = (event, nextCountry) => {
        if (!nextCountry) return;

        const { phone, country } = this.state;

        const prevPhone = country ? phone.replace(country.phones[0], '') : phone;
        const nextPhone = nextCountry.phones[0] + ' ' + prevPhone.trimStart();

        this.setState({ country: nextCountry, phone: nextPhone });
    };

    handleKeepChange = (event, value) => {
        this.setState({ keep: !this.state.keep });
    };

    handlePhoneChange = (event) => {
        this.enteredPhone = event.target.value;

        const nextPhone = event.target.value;

        let { country, data } = this.state;
        if (country) {
            if (!nextPhone.startsWith(country.phones[0])){
                country = null;
            }
        }

        if (!country && data) {
            const index = data.findIndex(x => nextPhone.startsWith(x.phones[0]));
            if (index !== -1) {
                country = data[index];
            }
        }

        this.setState({ phone: nextPhone, country });
    };

    render() {
        const { defaultPhone, classes, i18n, t } = this.props;
        const { connecting, loading, error, suggestedLanguage, data, keep, phone, country } = this.state;

        let errorString = '';
        if (error) {
            const { code, string } = error;
            if (code) {
                errorString = t(code);
            } else {
                errorString = string;
            }
        }

        const title = connecting ? cleanProgressStatus(t('Connecting')) : t('SignInToTelegram');
        const nextLanguage =
            suggestedLanguage === i18n.language ? LocalizationStore.defaultLanguage : suggestedLanguage;

        return (
            <div className={classNames('sign-in', classes.root)}>
                <Typography variant='body1' style={{ fontSize: 32, fontWeight: 500, margin: '0 auto 7px auto'}}>
                    <span>{title}</span>
                    {connecting && <HeaderProgress />}
                </Typography>
                <Typography variant='body1' style={{ color: '#707579', marginBottom: 38, width: 254, margin: '0 auto 38px auto', textAlign: 'center' }}>
                    {t('StartText')}
                </Typography>
                <Autocomplete
                    id='country-select'
                    style={{ width: 360 }}
                    options={data}
                    disabled={loading}
                    fullWidth
                    autoHighlight
                    getOptionLabel={option => option.name}
                    renderOption={option => <Country name={option.name} emoji={option.emoji} phone={option.phones[0]}/>}
                    renderInput={params => (
                        <TextField
                            {...params}
                            label={t('Country')}
                            variant='outlined'
                            inputProps={{
                                ...params.inputProps,
                                autoComplete: 'disabled'
                            }}
                        />
                    )}
                    filterOptions={this.handleFilterOptions}
                    value={country}
                    onChange={this.handleCountryChange}
                />
                <TextField
                    variant='outlined'
                    color='primary'
                    label={t('PhoneNumber')}
                    disabled={loading}
                    error={Boolean(errorString)}
                    helperText={errorString}
                    fullWidth
                    autoFocus
                    id='phoneNumber'
                    margin='normal'
                    autoComplete='off'
                    onKeyPress={this.handleKeyPress}
                    defaultValue={defaultPhone}
                    value={phone}
                    onChange={this.handlePhoneChange}
                />
                <div className='sign-in-keep'>
                    <Checkbox color='primary' checked={keep} disabled={loading} onChange={this.handleKeepChange}/>
                    <Typography variant='body1' >
                        {t('KeepMeSignedIn')}
                    </Typography>
                </div>
                <Button
                    variant='contained'
                    disableElevation
                    fullWidth
                    color='primary'
                    disabled={loading}
                    className={classes.button}
                    onClick={this.handleDone}>
                    {t('Next')}
                </Button>
                <Typography className={classes.continueAtLanguage}>
                    <Link onClick={this.handleChangeLanguage}>
                        {Boolean(nextLanguage) ? t('ContinueOnThisLanguage', { lng: nextLanguage }) : ' '}
                    </Link>
                </Typography>
            </div>
        );
    }
}

SignInControl.propTypes = {
    defaultPhone: PropTypes.string
};

const enhance = compose(
    withTranslation(),
    withStyles(styles, { withTheme: true })
);

export default enhance(SignInControl);
